/* dropdown button styling*/
.dropbutton {
    background-color: black;
    color: white;
    padding: 16px;
    font-size: 20px;
    border: none;
}
  
.dropdown {
    position: relative;
    display: inline-block;
}
  
.dropdown-content {
    display: none;
    position: absolute;
    background-color:black;
    font-family: 'Della Respira', serif;
    min-width: 160px;
    box-shadow: 0px 8px 8px 0px rgba(0,0,0,0.2);
    z-index: 1;
    color: white;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    font-family: 'Della Respira', serif;
    display: block;
    color: white;
}
.dropdown:hover .dropdown-content {
display: block;}

